  
import React,{useState, useEffect} from 'react'
import Carousel from 'react-elastic-carousel'
// import Propertyslider from '../Propertyslider/Propertyslider'
import Intro from '../intro/Intro'
import Footer from '../footer/Footer';
import { useTranslation} from "react-i18next";




const breakPoints=[{width:1,itemsToShow:1},
    {width:560,itemsToShow:2} ,
    {width:770,itemsToShow:3},
    {width:1200,itemsToShow:4}]

const Property = ({id}) => {


    const { t } = useTranslation();
  const[titlea, setTitlea]=useState('')
  const[location, setLocation]=useState('')
  const[prix, setPrix]=useState('')   
  const[type, setType]=useState('')
  const[nature, setNature]=useState('')
  const[images, setImages]=useState([])
  const [chambres,setChambres]=useState('')
const [salons,setSalons]=useState('')
    const [salle_bain,setSall_bain]=useState('')
    const [etage,setEtage]=useState('')
    const [n_etages,setN_etages]=useState('')
    const [zoning,setZoning]=useState('')
    const [surface,setSurface]=useState('')
    const [s_soupante,setS_soupante]=useState('')
    const [author_id,setAuthor_id]=useState('')
    const [description,setDescription]=useState('')
    
  
  

  useEffect(()=>{
    const fetchProperty = async(id)=>{
        const data1 = await getProperty(id)

        setTitlea(data1.title)
        setLocation(data1.city)
        setPrix(data1.price)
        setImages(data1.images)
        setType(data1.type)
        setSurface(data1.surface)
        setChambres(data1.chambres)
        setSalons(data1.salons)
        setS_soupante(data1.s_soupante)
        setSall_bain(data1.salle_bain)
        setNature(data1.element_type)
        // setAge_bien(data1.age_bien)
        setN_etages(data1.n_etages)
        setEtage(data1.etage)
        setDescription(data1.body)
        setS_soupante(data1.s_soupante)
        setZoning(data1.zoning)
        setAuthor_id(String(data1.author_id))

    }

    fetchProperty(id)

  },[])
//=======================> get property
  const getProperty=async(id)=>{
    const resp= await fetch(`/post/${id}`)
    const post= await resp.json()
    console.log(post)
    return post
  }

  const getUserInfo=async()=>{
    console.log('clicked')
    const resp= await fetch(`/api/user/${author_id}`)
    const user= await resp.json() 
    console.log(user) 
    document.getElementById('promoteur').innerHTML=`
    <div class="widget--content">
        <a href="#">
            <div class="agent--img">
                <img src=${user.photo} alt="agent" class="img-responsive"/>
            </div>
            <div class="agent--info">
                <h5 class="agent--title">${user.owner}</h5>
            </div>
        </a>
       
        <div class="agent--contact">
            <ul class="list-unstyled">
                <li><i class="fa fa-phone"></i>${user.phone}</li>
                <li><i class="fa fa-envelope-o"></i>${user.email}</li>
            </ul>
        </div>
     
     
    
</div>`
  }
getUserInfo() //added on 25/06

  return (
    <div>
        <Intro title={titlea}/>
        <section id="property-single-gallery" class="property-single-gallery">
                <div class="container">
                  {/* proprety info */}
                <div class="row">
                    <div class="col-xs-12 col-sm-12 col-md-12">
                        <div class="property-single-gallery-info">
                            <div class="property--info clearfix">
                                <div class="pull-left">
                                    <h5 class="property--title">{titlea}</h5>
                                    <p class="property--location"><i class="fa fa-map-marker"></i>{t(location)}</p>
                                    
                                </div>
                                <div class="pull-right">
                                    <span class="property--status" style={{"fontWeight":"bolder"}}>{t(nature)}</span>
                                    <p class="property--price">{prix} Dhs</p>
                                </div>
                            </div>  
                            </div>
                    </div>
            </div>
        {/* carousel */}
                <div class="row">
                    <div class="col-xs-12 col-sm-12 col-md-8">
                        <div class="property-single-carousel inner-box">
                            <div class="row">
                                <div class="col-xs-12 col-sm-12 col-md-12">
                                    <div class="heading">
                                        <h2 class="heading--title">{t("Property Gallery")}</h2>
                                    </div>
                                </div>

                                
                             
                                <div class="col-xs-12 col-sm-12 col-md-12">
                                  <Carousel breakPoints={breakPoints}>
                                            {images.map((image) => (<img src={image} alt="Property Image" style={{"height":"20rem"}}/>))}
                                           
                                     
                                    </Carousel>
                                   
                                {/* {images.length > 0 ? <Propertyslider images={images}/>: <span>pas d'images</span>} */}
                            </div>
                          
                        </div>
                        <div class="property-single-desc inner-box">
                            <div class="row">
                                <div class="col-xs-12 col-sm-12 col-md-12">
                                    <div class="heading">
                                        <h2 class="heading--title">{t("Description")}</h2>
                                    </div>
                                </div>
                           
                                <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/1.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("surface")}</h5>
                                            <p>{surface}</p>
                                        </div>
                                    </div>
                                </div>
        {  s_soupante &&           <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/1.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("surface soupante")}</h5>
                                            <p>{s_soupante}</p>
                                        </div>
                                    </div>
                                </div>}
         { chambres &&        <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/2.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("chambres")}</h5>
                                            <p>{chambres}</p>
                                        </div>
                                    </div>
                                </div>}
                      
           {  salle_bain &&       <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/3.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("salle de  bains")}</h5>
                                            <p>{salle_bain}</p>
                                        </div>
                                    </div>
                                </div>}
                      
            {  etage &&      <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/4.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("Etage")}</h5>
                                            <p>{etage}</p>
                                        </div>
                                    </div>
                                </div>}
                       
      {  n_etages &&        <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/5.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("Nombre d'Etages")}</h5>
                                            <p>{n_etages}</p>
                                        </div>
                                    </div>
                                </div>}
          
        { salons &&            <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/7.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("salons")}</h5>
                                            <p>{salons}</p>
                                        </div>
                                    </div>
                                </div>}
                                <div class="col-xs-6 col-sm-4 col-md-4">
                                    <div class="feature-panel">
                                        <div class="feature--img">
                                            <img src="assets/images/property-single/features/6.png" alt="icon"/>
                                        </div>
                                        <div class="feature--content">
                                            <h5>{t("Zone")}</h5>
                                            <p>{zoning}</p>
                                        </div>
                                    </div>
                                </div>                             
                                <div class="col-xs-12 col-sm-12 col-md-12">
                                    <div class="property--details">
                                        <p>{description}</p>
                                        
                                    </div>
                               
                                </div>
                                
                              
                            </div>
                        
                        </div>
                    </div>
                    </div>
                    <div class="col-xs-12 col-sm-12 col-md-4">
                    <div class="widget widget-property-agent">
    <div class="widget--title">
        <h5>{t("About Agent")}</h5>
    </div>
                        <div id='promoteur'></div>
                        {/* <button className='btn' onClick={getUserInfo} clicked>voir promoteur</button> */}
                </div>
                </div>
                </div>
                </div>
    </section>
    <Footer />
    </div>
  )
}

export default Property
